
.file-upload-label {
    display: inline-block;
    position: relative;
    margin: 0 !important;
    padding: 0 !important;
}

.file-upload-btn {
    margin-bottom: 0 !important;
}

.file-upload-btn + input {
    opacity: 0;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
}



